<template>
    <div class='firststep'>
        <img class='headerimg' src="@/assets/img/home/guide/step1_header.png" alt="">
        <div class='text'>
            远古时期，6只圣耀种族精灵与6只灾厄种族精灵发生了战争， 其他种族精灵纷纷加入战争，后续精灵的故事由此开始……
        </div>
        <div class='buttons'>
            <span @click='nextPress'></span>
        </div>
    </div>
</template>
<script>
export default {
    name:'newStep',
    data(){
        return{

        }
    },
    mounted(){},
    methods:{
        nextPress(){
            this.$router.push({path:'/choseSprinte'})
        },
    },

}
</script>
<style lang="less" scoped>
@import '../../../common/css/pulic.less';
    .firststep{
        width:100%;
        height:100%;
       .layer-background(@backgroundColor);
    }
    .headerimg{
        width:100%;
        height:auto;
        margin-bottom:47px;
    }
    .text{
        font-size:22px;
        line-height:40px;
        color:white;
        padding:0 53px ;
        .box-sizing(@border-box);
        margin-bottom:135px;
    }
    .buttons{
        width:100%;
        height:auto;
        span{
            display:block;
            width:190px;
            height:74px;
            margin:0 auto;
            background:url('../../../assets/img/home/guide/next-button.png') no-repeat;
            background-size:100% 100%;
        }
    }
</style>


